@import url(https://fonts.googleapis.com/css?family=Bungee+Hairline|Comfortaa:700|Libre+Baskerville:700);
* {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	box-sizing: border-box;
}
html {
	-webkit-animation: color-change-3x 4s linear infinite alternate both;
	animation: color-change-3x 4s linear infinite alternate both;
	background-color: transparent !important;
	font-size: 10px;
	width: 100%;
	height:100%;
	margin: 0;
	padding: 0;
}
body {
	font-family: 'Comfortaa', cursive;
	margin: 0;
	padding: 0;
	width: 100%;
	height:100%;
}
#root {
	margin: 0;
	padding: 0;
	width: 100%;
	height:100%;
    overflow: hidden;
    position:fixed;
}
#super {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	margin: 0;
	padding: 5%;
	width: 100%;
	height:100%;
}
#intro {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding:0;
	margin: 0;
}

#mobile-intro {
	display: none;
}

/* -------------------------------------------- RBC CSS -------------------------------------------- */

#rbc {
	padding: 0;
	margin: 0;
	-webkit-animation: bounce-in-bottom 1.1s both;
	animation: bounce-in-bottom 1.1s both;
	-webkit-animation-delay:7.2s;
	animation-delay:7.2s;
}
.rbc-outer {
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}
.rbc-wrapper {
	position: relative;
	margin: 0;
	padding: 0;
	-webkit-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
}
.rbc-shadow {
	width: 90%;
	height: 90%;
	position: absolute;
	top: 5%;
	left: 5%;
	bottom: 0;
	right: 0;
	background: none;
	border-radius: 10px;
	border: 1px solid #e1e1e1;
	-webkit-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
}
.rbc-layers {
	width: 100%;
	height: 100%;
}
.rbc-layer-solo {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}
.rbc-layer-00 {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}
img {
	border-radius: 2.75rem;
}
.rbc-layer-01 {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	/* display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin: 0;
	padding: 0; */
	color: #ffffff;
	z-index:1;
}
.text-wrapper {
	display: flex;
	flex-wrap: wrap;
	/* position: relative; */
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	/* height: 100%; */
	justify-content: center;
	/* padding: 0; */
	/* margin: 0; */
	/* width: 100%; */
}

/* ------------------------------------- RBC CARD TEXT & ICONS ------------------------------------- */

.full_name,
.title,
.email {
	flex-basis: 100%;
	text-align: center;
	text-shadow: inherit;
}
.full_name {
	font-family: 'Libre Baskerville', serif;
	font-size: 4em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
}
.title {
	font-size: 3.5em;
	letter-spacing: 0.05em;
    -webkit-margin-before: 0.3em;
            margin-block-start: 0.3em;
    -webkit-margin-after: 0;
            margin-block-end: 0;
}
.email {
	font-size: 2.5em;
	letter-spacing: 0.15em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    -webkit-margin-before: .5em;
            margin-block-start: .5em;
}
.email > a {
	text-decoration: none;
	color: rgb(255, 255, 255);
}
.link_buttons {
	display: flex;
	flex-basis: 75%;
	flex-direction: row;
	justify-content: space-around;
}
.link_button {
	background:none!important;
	border:none;
	align-items: center;
	cursor: pointer;
	display: flex;
	font-size: 2em;
	margin: 1em 0.7em;
	text-shadow: inherit;
	color: rgb(255, 255, 255);
}
/* -------------------------------------------------------------------- */

.rbc-lighting {
	border-radius: 2.75rem;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}
/* -------------------------------------------------------------------- */

#rotate-device {
		display: none;
		padding: 0;
		margin: 0;
}
#screen-too-small {
		display: none;
		padding: 0;
		margin: 0;
}
/* ------------------------------------------- MEDIA Q'S ------------------------------------------- */
/* display a div saying: "I'm sorry your device is screen is too small to view this page" */

@media screen and (max-width: 150px) and (orientation: portrait){
	#screen-too-small {
		display: flex;
		background-color:crimson;
		padding:5%;
		width: 90vw;
		height: 90vw;
	}
	#intro {
		display: none;
	}
	#rbc {
		display: none;
	}
}
@media screen and (max-height: 150px) and (orientation: landscape) {
	#screen-too-small {
		display: flex;
		background-color:crimson;
		padding:5%;
		width: 90vh;
		height: 90vh;
	}
	#intro {
		display: none;
	}
	#rbc {
		display: none;
	}
}
/*when its not tall enough to accomodate both */
@media screen and (max-height: 750px) {
	#intro {
		display: none;
	}
	#rbc {
		-webkit-animation-delay:0s;
		animation-delay:0s;
	}
}
/*when its not wide enough to accomodate the intro text, because the card itself resizes automatically */
@media screen and (max-width: 650px) {
	#intro {
		display: none;
	}
	#rbc {
		-webkit-animation-delay:0s;
		animation-delay:0s;
	}
}
